body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

h1, h2, h3, h4, h5 {
  line-height: normal;
  line-height: initial
}

#root {
  position: relative;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.fixfloat:after {
  content: "";
  display: table;
  clear: both;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: scale(1); transform: scale(1); }
  to { -webkit-transform: scale(1.08); transform: scale(1.08); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: scale(1); transform: scale(1); }
  to { -webkit-transform: scale(1.08); transform: scale(1.08); }
}

/* fix semantic-ui quirks */
.ui.negative.floating.message, .ui.attached.negative.floating.message {
  box-shadow: 
    0px 0px 0px 1px #E0B4B4 inset, 
    0px 2px 4px 0px rgba(34, 36, 38, 0.12), 
    0px 2px 10px 0px rgba(34, 36, 38, 0.15)
}
